<template>
  <div class="modal">
    <div :class="`${type}`" class="modal__window">
      <slot />
    </div>
    <div @click="$emit('close')" class="modal__overlay"></div>
  </div>
</template>


<script>

  export default {
    name: 'Modal',
    props: {
      type: {
        type: String,
        default: 'default',
      }
    },
  }

</script>

<style lang="scss">

  .modal-enter-active, .modal-leave-active {
    transition: opacity .5s;
  }

  .modal-enter, .modal-leave-to {
    opacity: 0;
  }

  .modal {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: block;
    z-index: 2000;

    &__overlay {
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background: rgba(0, 0, 0, 0.5);
    }

    &__window {
      position: absolute;
      background-color: #fff;
      z-index: 1500;

      &.default {
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);

        @media (max-width: 640px) {
          & {
            left: 10%;
            transform: translate(-5%,-50%) 
          }
        }
      }

      &.full {
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
      }

    }
  }
</style>